import { Entity, NgField, NgObject, EntityList, NgList } from '@farris/devkit';

/**
 * 
 */
export class AuresultEntity extends Entity {

    /**
     * Id
     */
    @NgField({
        dataField: 'packageId',
    })
    packageId: string;

    /**
     * Id
     */
    @NgField({
        dataField: 'authField'
    })
    authField: string;

    /**
     * 
     */
    @NgField({
        dataField: 'groupId',
        primary: true
    })
    groupId: string;


    /**
     * 
     */
    @NgField({
        dataField: 'datas'
    })
    datas: BizDataDTO[];


    /**
     *  Id
     */
    @NgField({
        dataField: 'authorizations',
    })
    authorizations: AuthorizationEntity[];


    /**
    *  
    */
    @NgField({
        dataField: 'ruleEntity',
    })
    ruleEntity: GSPAuthorizationRule[];
}

/**
 * 
 */
export class AuthorizationEntity {
    id: string;
    code: string;
    name: string;
    authOps: AuthoperationEntity[];
}

/**
 * 
 */
export class AuthoperationEntity {
    id: string;
    code: string;
    name: string;
}

/**
 * 
 */
export class GSPAuthorizationRule {
    Type: number;  //  12
    NeedInput: boolean;  //
    Lbracket: string; // 
    Rbracket: string; // 
    Variable: string; // 
    Value: string; // 
    DisplayValue: string; // 
    Relation: string; // 
}


/**
 *   
 */
export class BizDataDTO {
    assid1: string;
    assid2: string;
    assid3: string;
    data: string;
}



