import { Injectable, Injector } from '@angular/core';
import { NgRepository, Repository } from '@farris/devkit';
import { AuresultDatastorage } from './auresult-datastorage';
import { AuresultEntity } from './entities/auresult.entity';

@Injectable()
@NgRepository({
  from : AuresultDatastorage,
  entityType : AuresultEntity
})
class AuresultRepository extends Repository<AuresultEntity> {
  constructor(injector: Injector) {
    super(injector);
  }
}

export { AuresultRepository };



