import { Injectable } from '@angular/core';
import { HttpHeaders } from '@angular/common/http';

@Injectable()
export class AuthassignSession {
    public sessionId: string;
    // public currentUrl = 'http://127.0.0.1:5000';
    // public currentUrl = window.localStorage.getItem('ip');

    //   TODO 
    public ownerId: string;

    //   TODO 
    public ownerType: number;

    //   TODO 
    public authField: string;

    //  TODO 
    public packageId: string;

    //   TODO 
    public year: string;

    //   TODO Farris 
    public addGroupIds: string[] = new Array<string>();

    //   TODO Farris 
    public delGroupIds: string[] = new Array<string>();

    // ID
    public funcId: string;


      /**
     * 
     */
    getPositionManagerRouter (): string {
        // user-manager
        const frameWorkUrl = '/main/position-manager/' + this.funcId + '/positions';
        return frameWorkUrl;
    }

    /**
    * 
    */
    getResultPath(): any {
        const path = `/api/runtime/sys/v1.0/assignresults`;
        return path;
    }

    /**
    * 
    */
    getAuthFieldPath(): any {
        const path = `/api/runtime/sys/v1.0/authFields`;
        return path;
    }

    /**
    * 
    */
    getAuthorizationPath(): any {
        const path = `/api/runtime/sys/v1.0/authorizations`;
        return path;
    }


    /**
     * Header
     */
    getHeader() {

        //  session
        // const headers = new HttpHeaders().set('SessionId', '26715fcc-862f-4f5d-ac6b-38e8ff0d0a7e');
        const headers = new HttpHeaders().set('SessionId', this.sessionId);
        return { headers };
    }

    /**
     * 
     */
    getCurrentUserId(): string {

        // 
        return 'lilitr';
    }
}



