
import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler, TaskExecutionContext } from '@farris/devkit';
import { AuresultService } from '../../domain/index';
import { AuresultStatemachine } from '../auresult-statemachine';
import { AuresultUistate } from '../auresult-uistate';
import { AuresultBindingdata } from '../../app/auresult-bindingdata';

@Injectable()
@NgCommandHandler({
  commandName: 'loadResultData'
})
class AuresultLoadHandler extends CommandHandler {

  constructor(
    private dataService: AuresultService,
    private uiState: AuresultUistate,
    private stateMachine: AuresultStatemachine,
    private bindingData: AuresultBindingdata
  ) {
    super();
  }

  schedule() {

    // 
    this.addTask('loadData', (authfield: string) => {
      this.uiState.action = 'load';
      this.dataService.getResultdata();
    });

  }
}

export { AuresultLoadHandler };



