import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { CommandHandler, NgCommandHandler, TaskExecutionContext } from '@farris/devkit';
import { AuthassignSession } from '../../../shared/session';


@Injectable()
@NgCommandHandler({
  commandName: 'close'
})
class CloseHandler extends CommandHandler {

  constructor(
    private router: Router,
    private session: AuthassignSession
  ) {
    super();
  }

  schedule() {
    this.addTask('close', () => {
     // pos-manager
     const posUrl = '/main/position-manager/' + this.session.funcId + '/positions';
      this.router.navigate([posUrl]);
    });
  }
}

export { CloseHandler };



