import { Entity, NgField, NgObject, EntityList, NgList } from '@farris/devkit';

/**
 * 
 */
export class AuthfieldEntity extends Entity {

    /**
     * Id
     */
    @NgField({
        dataField: 'id',
        primary: true
    })
    FID: string;

    /**
     * code
     */
    @NgField({
        dataField: 'code'
    })
    FCode: string;

    /**
     * name
     */
    @NgField({
        dataField: 'name'
    })
    FName: string;


    /**
     * 01
     */
    @NgField({
        dataField: 'type'
    })
    FType: string;


    /**
     *   //NgFieldNgListNgObject,id
     */
    @NgField({
        dataField: 'parentId',
    })
    FParentID: string;
}



