import { Injectable } from '@angular/core';
import { AuthFieldTreeDatastorage } from './authfield-tree-datastorage';
import { authFieldData } from './entities/authfielddata';
import { RestfulService } from '@farris/devkit';
import { catchError, map, tap } from 'rxjs/operators';
import { HttpHeaders } from '@angular/common/http';
import { AuthassignSession } from '../../shared/session';

@Injectable()
export class AuthfieldService {
    constructor(
        public rest: RestfulService,
        public dataStorage: AuthFieldTreeDatastorage
        , public session: AuthassignSession
    ) { }

    getdata(): any {
        const headers = this.session.getHeader();
        const url = this.session.getAuthFieldPath();
        this.rest.get(url, null, headers).subscribe((res: any) => {
            console.log(res);
            if (res instanceof Array) {
                res.forEach((item) => { // kendoUi treeview parentid  undefined
                    if (item.parentId === '') {
                        item.parentId = undefined;
                    }
                });
                this.dataStorage.load(res);
            } else {
                if (res.parentId === '') {
                    res.parentId = undefined;
                }
                this.dataStorage.load([res]);
            }
        });
    }

}



