import { Provider } from '@angular/core';
import { AuresultBindingdata } from './auresult-bindingdata';
import { AuresultForm } from './auresult-form';
import { AuresultStatemachine } from './auresult-statemachine';
import { AuresultViewmodel } from './auresult.viewmodel';
import { AuresultUistate } from './auresult-uistate';
import { AuresultCommandHandlers } from './handler/index';


const AuresultAPP_PROVIDERS: Provider[] = [
      AuresultBindingdata,
      AuresultForm,
      AuresultUistate,
      AuresultViewmodel,
      AuresultStatemachine
];

export {
      AuresultBindingdata,
      AuresultStatemachine,
      AuresultForm,
      AuresultUistate,
      AuresultViewmodel,
      AuresultAPP_PROVIDERS,
      AuresultCommandHandlers
};



