import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { AuresultDatastorage } from './auresult-datastorage';
import { RestfulService } from '@farris/devkit';
import { HttpHeaders, HttpParams } from '@angular/common/http';
import { AuthassignSession } from '../../shared/session';
import { AuthopHelpEntity } from './entities/authop-help.entity';
import { AuresultChangesEntity } from '../domain/entities/auresut-changes.entity';
import { AuthDataSource } from './entities/auth-data-source.entity';
import { isNullOrUndefined } from 'util';

@Injectable()
export class AuresultService {

    constructor(
        private rest: RestfulService,
        public dataStorage: AuresultDatastorage,
        private session: AuthassignSession
    ) { }

    changeResultdata(): any {
        this.dataStorage.load(null);
    }

    /**
    * 
    */
    getResultdata(): any {
        // mock
        // this.dataStorage.load(auresultData);
        const authfield = this.session.authField;
        console.log('authfield:' + authfield);
        const ownerId = this.session.ownerId;
        console.log('ownerIdF:' + ownerId);
        const ownerType = this.session.ownerType;
        console.log('ownerType:' + ownerType);
        const headers = this.session.getHeader();
        const url = this.session.getResultPath();

        // TODO
        const curUserId = this.session.getCurrentUserId();
        const urlNew = url + '?currentUserId=' + curUserId + '&ownerId=' + ownerId + '&ownerType=' + ownerType.toString()
            + '&authfield=' + authfield;
        this.rest.get(urlNew, null, headers).subscribe((result: any) => {
            this.dataStorage.load(result);
        });
    }

    /**
    * 
    */
    getDataSource(authField: string): Observable<any> {
        // mock
        // return of(dataSource);
        const authDataSource = new AuthDataSource();
        const headers = this.session.getHeader();
        const url = this.session.getResultPath();
        const urlNew = url + '/dataSource/' + authField;
        return this.rest.get(urlNew, null, headers);
    }


    /**
    * 
    */
    saveResultData(body: AuresultChangesEntity): Observable<any> {
        const headers = this.session.getHeader();
        console.log(headers);
        const url = this.session.getResultPath();
        return this.rest.post(url, body, null, headers);
    }

    /**
    * 
    */
    getAuthorization(authfield: string): any {
        const headers = this.session.getHeader();
        const url = this.session.getAuthorizationPath();
        const urlNew = url + '/authfield/' + authfield;
        const authorizations = new Array<AuthopHelpEntity>();
        console.log('urlNew' + urlNew);
        this.rest.get(urlNew, null, headers).subscribe((datas: any) => {
            let arrData = new Array();
            arrData = datas;
            arrData.forEach((data) => {
                const obj = new AuthopHelpEntity();
                obj.identiy = data.id + '~' + data.name;
                obj.id = data.id;
                obj.name = data.name;
                // if (data.parentId === '') {
                //     obj.parentId = undefined;
                // } else {
                //     obj.parentId = data.parentId;
                // }
                obj.type = 0;
                obj.authOps = new Array<AuthopHelpEntity>();
                if (!isNullOrUndefined(data.authOps) && data.authOps.length !== 0) {
                    data.authOps.forEach((child) => {
                        const op = new AuthopHelpEntity();
                        op.identiy = data.id + '~' + data.name + '~' + child.id + '~' + child.name;
                        op.id = child.id;
                        op.name = child.name;
                        op.type = 1;
                        op.parentId = data.id;
                        obj.authOps.push(op);
                    });
                    // TODO  
                    obj.parentId = undefined;
                    authorizations.push(obj);
                }
            });
        });
        console.log('authorizations:' + authorizations);
        return authorizations;
    }

    /**
    * TODO ,Farris
    */
    addGroups(groupId: string): void {
        this.session.addGroupIds.push(groupId);
        const index = this.session.delGroupIds.indexOf(groupId);
        if (index > -1) {
            this.session.delGroupIds.splice(index, 1);
        }
    }

    /**
     * TODO ,Farris
     */
    delGroups(groupId: string): void {
        this.session.delGroupIds.push(groupId);
        const index = this.session.addGroupIds.indexOf(groupId);
        if (index > -1) {
            this.session.addGroupIds.splice(index, 1);
        }
    }
}

