import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthassignViewComponent } from './authassign-view/authassign-view.component';

const routes: Routes = [
  { path: '', component: AuthassignViewComponent }
];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
class AuthassignRoutingModule {}

export { AuthassignRoutingModule };



