import { Injectable, Injector } from '@angular/core';
import { NgBindingData, BindingData } from '@farris/devkit';
import { AuresultRepository, AuresultEntity } from '../domain/index';

@Injectable()
@NgBindingData({
  entity: AuresultEntity,
  repository: AuresultRepository
})
class AuresultBindingdata extends BindingData {
  constructor(injector: Injector) {
    super(injector);
  }
}

export { AuresultBindingdata
};



