import { Injectable } from '@angular/core';
import { StateMachine, NgStatemachine, State, NgState, RenderState, NgRenderState, Action, NgAction } from '@farris/devkit';

@Injectable()
@NgStatemachine()
class AuresultStatemachine extends StateMachine {

    /**
     * --------------------------------------------------------------------------------
     * 
     * --------------------------------------------------------------------------------
     */

    /**
     * 
     */
    @NgState({ initialState: true })
    viewState: State;

     /**
     * 
     */
    @NgState()
    editState: State;


  /**
   * 
   */
  @NgRenderState({
    render: (context) => context.state === 'editState'
  })
  canInput: RenderState;

  /**
   * --------------------------------------------------------------------------------
   * 
   * --------------------------------------------------------------------------------
   */
    /**
   * 
   */
  @NgRenderState({
    render: (context) => (context.state === 'viewState' || context.state === 'editState')
  })
  canEdit: RenderState;

    /**
   * 
   */
  @NgRenderState({
    render: (context) => (context.state === 'viewState' || context.state === 'editState')
  })
  canSave: RenderState;

   /**
   * --------------------------------------------------------------------------------
   * 
   * --------------------------------------------------------------------------------
   */
  @NgAction({transitTo: 'editState' })
  load: Action;

  @NgAction({transitTo: 'editState' })
  add: Action;

  @NgAction({ transitTo: 'editState' })
  edit: Action;

  @NgAction({ transitTo: 'editState' })
  view: Action;

  @NgAction({ transitTo: 'editState' })
  save: Action;

  @NgAction({ transitTo: 'editState' })
  clear: Action;

  @NgAction({ transitTo: 'editState' })
  close: Action;

}

export { AuresultStatemachine
};



