import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler, TaskExecutionContext } from '@farris/devkit';
import { AuthfieldService } from '../../domain/index';
// import { AuthfieldTreeUistate } from '../../app/authfield-tree-uistate';
// import { AuthfieldTreeStatemachine } from '../../app/authfield-tree-statemachine';
@Injectable()
@NgCommandHandler({
  commandName: 'formLoad'
})
class AuthfieldLoadHandler extends CommandHandler {

  constructor(
    public dataService: AuthfieldService
    // private uiState: AuthfieldTreeUistate,
    // private stateMachine: AuthfieldTreeStatemachine
  ) {
    super();
  }

  schedule() {

    // 
    this.addTask('loadData', () => {
        // this.uiState.action = 'load';
        this.dataService.getdata();
    });

  }
}

export { AuthfieldLoadHandler };



