import { Injector, Injectable } from '@angular/core';
import { NgViewModel, ViewModel,  NgCommand } from '@farris/devkit';

import { AuresultBindingdata } from './auresult-bindingdata';
import { AuresultForm } from './auresult-form';
import { AuresultStatemachine } from './auresult-statemachine';
import { AuresultUistate } from './auresult-uistate';

/**
 * 
 */
@Injectable()
@NgViewModel({
  children: [],
  binding: AuresultBindingdata,
  form: AuresultForm,
  stateMachine: AuresultStatemachine,
})
class AuresultViewmodel extends ViewModel {
public currentAuthfield: string;
public uiState: AuresultUistate;
public stateMachine: AuresultStatemachine;

  /**
   * 
   *
   */
  constructor(injector: Injector) {
    super(injector);
    this.uiState = this.injector.get(AuresultUistate);
  }

  /**
   * 
   */
  @NgCommand({
    name: 'loadResultData',
    params: {
        authfield: '' // TODO WEB
      }
   })
  public loadResultData() {}

   /**
   * 
   */
  @NgCommand({
    name: 'add',
    params: {
      authfield: '', // TODO WEB
      pakageId: '' // TODO WEB
    }
   })
  public add() {}

   /**
   * 
   */
  @NgCommand({
    name: 'edit'
   })
  public edit() {}


   /**
   * 
   */
  @NgCommand({
    name: 'save'
   })
  public save() {}

   /**
   * 
   */
  @NgCommand({
    name: 'change'
   })
  public change() {}

  /**
   * 
   */
  @NgCommand({
    name: 'close'
   })
  public close() {}
}

export { AuresultViewmodel };



