import { Component, OnInit } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { AuthfieldTreeViewmodel } from '../authfield-tree/app/index';
import { Change, ChangeType, PARAMETERS, EntityList } from '@farris/devkit';
import { AuresultEntity, AuthorizationEntity, AuthoperationEntity, AuresultRepository } from '../auresultgroup/domain/index';
import { AuthopHelpEntity } from '../auresultgroup/domain/index';
import { AuresultViewmodel } from '../auresultgroup/app/index';
import { LayoutModule } from '@progress/kendo-angular-layout';
import { PanelBarModule, PanelBarItemModel } from '@progress/kendo-angular-layout';
import { ActivatedRoute, Params } from '@angular/router';
import { CheckableSettings, CheckedState } from '@progress/kendo-angular-treeview';
import { GridModule, SelectableSettings } from '@progress/kendo-angular-grid';
import { AuresultService } from '../auresultgroup/domain/auresult-service';
import { isNullOrUndefined, isNull } from 'util';
import { AuthassignSession } from '../shared/session';
import { BizDataDTO } from '../auresultgroup/domain/entities/auresult.entity';
import { AuthDataSource, AuthDataField } from '../auresultgroup/domain/entities/auth-data-source.entity';
import { FrameworkService } from '@gsp-sys/rtf-common';

@Component({
  selector: 'app-authassign-view',
  templateUrl: './authassign-view.component.html',
  styleUrls: ['./authassign-view.component.css']
})
export class AuthassignViewComponent implements OnInit {

  funcid: string;
  dataAuthfield: any[];
  currentAuthfield: string;
  expandedKeys: string[] = ['ERP'];


  // 
  // auresults: EntityList<AuresultEntity> = new EntityList<AuresultEntity>();
  auresults: AuresultEntity[];
  curResultGroup: AuresultEntity;
  curResultGroupSelect: AuresultEntity;
  dataItems: Array<PanelBarItemModel> = [];


  // 
  public checkedKeys: any[] = [];
  public key = 'identiy';
  // 
  czTreeNavDialog = false;
  canEditOp = false;
  canAssigAuthorization = new Array<AuthopHelpEntity>();

  // 
  canEditData = false;
  isAllData = false;
  public checkedDataKeys: any[] = [];
  dataTreeNavDialog = false;
  // 
  dataKey = ''; // 
  helpFields: string[] = []; // 
  showFields: AuthDataField[] = []; // 
  canAssignDatas: any[] = []; // 
  authDataSource: AuthDataSource = new AuthDataSource();

  public fetchChildren(node: any): Observable<any[]> {
    return of(node.authOps);
  }

  public children = (dataItem: any): Observable<any[]> => of(dataItem.authOps);
  public hasChildren = (dataItem: any): boolean => !!dataItem.authOps;

  constructor(
    public viewmodelAuthfield: AuthfieldTreeViewmodel,
    public viewmodelAuresult: AuresultViewmodel,
    public route: ActivatedRoute,
    public resultService: AuresultService,
    public resultRepository: AuresultRepository,
    private currentSession: AuthassignSession,
    private frameworkService: FrameworkService
  ) { }


  ngOnInit() {

    // TODO:
    // 1
    // 2CommandCommandHandler
    // 3CommandHandlerswitchMap
    this.route.paramMap.subscribe((params: Params) => {
      if (params.has('funcid')) {
        this.funcid = params.get('funcid');
        this.currentSession.funcId = this.funcid;
        this.currentSession.sessionId = this.frameworkService.getSession(this.funcid);
        this.viewmodelAuresult.uiState.action = 'view'; // params.get('action'); // 'view'; // params.get('action');
        this.currentSession.ownerId = params.get('ownerId'); // 'BE'; // 'BE'; //
        this.currentSession.ownerType = params.get('type'); // 1; //
      }
    });

    // OpenFunc 
    // this.route.queryParams.subscribe((params: Params) => {
    //   if (params['funcParam']) {
    //     const paramObj = params['funcParam'];
    //     console.log(paramObj);
    //     this.funcid = paramObj.funcId;
    //     this.currentSession.funcId = this.funcid;
    //     this.currentSession.sessionId = this.frameworkService.getSession(this.funcid);
    //     this.viewmodelAuresult.uiState.action = 'view'; // params.get('action'); // 'view'; // params.get('action');
    //     this.currentSession.ownerId = paramObj.ownerId; // 'BE'; // 'BE'; //
    //     this.currentSession.ownerType = paramObj.type; // 1; //
    //   }
    // });

    // 
    console.log('sessinonId:' + this.currentSession.sessionId);
    console.log('ownerid:' + this.currentSession.ownerId);
    console.log('ownertype:' + this.currentSession.ownerType);

    this.viewmodelAuthfield.bindingData.changes.subscribe((changes: Change) => {
      console.log('changetype =' + changes.type);
      if (changes.type === ChangeType.SelectionChanged) {
        return;
      }
      const a = changes;
      this.dataAuthfield = this.viewmodelAuthfield.bindingData.list.toJSON();
      console.log(JSON.stringify(this.dataAuthfield));
      // 
      this.dataAuthfield.forEach(x => {
        if (isNullOrUndefined(x.FParentID)) {
          this.expandedKeys.push(x.FID);
        }
      });
      console.log('');
      console.log(this.expandedKeys);
    });


    this.viewmodelAuresult.bindingData.changes.subscribe(
      (changes: Change) => {
        console.log('changetypeAuresult =' + changes.type);
        this.buildAuresult();
        console.log(JSON.stringify(this.auresults));
        console.log('after changed auresult');
        this.BuildResultPanel();
      }
    );

    this.viewmodelAuthfield.formLoad();
  }


  /**************************----------*********************************************/
  // 
  handleSelection({ dataItem }: any): void {
    if (dataItem.FType === '0') {
      return;
    }
    const field = dataItem.FID;
    // TODO Command
    this.viewmodelAuthfield.bindingData.list.setCurrentId(field);
    this.currentSession.authField = field;
    this.viewmodelAuresult.currentAuthfield = field;
    this.viewmodelAuresult.loadResultData();

    // TODO 
    this.getCanAssignAuthorization(field);
    this.getAuthDataSource(field);
    this.buildAuresult();
    console.log('after load auresult');
    this.BuildResultPanel();
  }

  buildAuresult(): void {
    // const data = this.viewmodelAuresult.bindingData.list.toJSON().map(row => new AuresultEntity(row));
    // const data = this.viewmodelAuresult.bindingData.list.toJSON();
    // this.auresults.loadEntities(data);
    this.auresults = this.viewmodelAuresult.bindingData.list.toJSON();
  }

  // 
  getCanAssignAuthorization(authField: string): void {
    this.canAssigAuthorization = this.resultService.getAuthorization(authField);
    console.log('');
    console.log(this.canAssigAuthorization);
  }

  // 
  getAuthDataSource(authField: string): void {
    // 
    this.resultService.getDataSource(authField)
      .subscribe(
        (data) => {
          this.authDataSource.data = data.data;
          this.authDataSource.showFields = data.showFields;
          this.authDataSource.keyField = data.keyField;
          this.authDataSource.helpFields = data.helpFields;
          this.canAssignDatas = this.authDataSource.data;
          this.dataKey = this.authDataSource.keyField;
          this.helpFields = this.authDataSource.helpFields;
          this.showFields = this.authDataSource.showFields;
          console.log('');
          console.log(this.authDataSource);
          console.log(this.canAssignDatas);
          console.log(this.showFields);
        }
      );
  }


  iconClass({ FType }: any): any {
    return {
      'k-i-folder': FType.toString() === '0',
      'k-i-gear': FType.toString() === '1',
      'k-icon': true
    };
  }
  /**************************----------*********************************************/

  // 
  BuildResultPanel() {
    this.dataItems = Array<PanelBarItemModel>();
    this.auresults.forEach((result: AuresultEntity) => {
      const item = <PanelBarItemModel>{
        title: result.groupId, content: result,
        expanded: true
      };
      this.dataItems.push(item);
    });
  }

  /**************************----------*********************************************/
  // TODO 
  openAuthOpDialog(): void {
    // TODO 
  }

  // 
  getCssOp(): string {
    if (this.canEditOp === true) {
      return 'btn btn-sm  mr-2  btn-primary';
    } else {
      return 'btn btn-sm  mr-2';
    }
  }

  // TODO 
  BuildDisplayAuthops(authOps: AuthoperationEntity[]): string[] {
    if (isNullOrUndefined(authOps)) {
      return null;
    }
    const authopNames = new Array<string>();
    authOps.forEach(x => { authopNames.push(x.name); });
    return authopNames;
  }


  // TODO 
  showAuthopsHelp(groupId: string, canEdit: boolean): void {
    this.canEditOp = canEdit;

    this.checkedKeys = [];
    this.czTreeNavDialog = true;
    this.curResultGroup = this.auresults.find(x => x.groupId === groupId);
    this.curResultGroup.authorizations.forEach(element => {
      // this.checkedKeys.push(element.id + '~' + element.name);
      if (isNullOrUndefined(element.authOps) === false) {
        element.authOps.forEach((op) => { this.checkedKeys.push(element.id + '~' + element.name + '~' + op.id + '~' + op.name); });
      }
    });
    this.curResultGroupSelect = this.curResultGroup;

    // TODO 
  }
  //  
  closeAuthopsHelp(): void {
    this.czTreeNavDialog = false;
    this.curResultGroup.authorizations = [];
    const authArray = [];
    let i = 0;

    // 
    this.checkedKeys.forEach((dataitem) => {
      const s = dataitem.split('~');
      if (authArray.length === 0) {
        authArray.push({ id: s[0], name: s[1], authOps: [] });
        i++;
        //   
      } else if (!(authArray[i - 1].id === s[0])) {
        authArray.push({ id: s[0], name: s[1], authOps: [] });
        i++;
      }
    });

    // 
    authArray.forEach((authorization) => {
      const authOps = this.checkedKeys.filter(x => x.split('~').length === 4 && x.split('~')[0] === authorization.id);
      if (!isNullOrUndefined(authOps)) {
        authOps.forEach((dataitem) => {
          const op = dataitem.split('~');
          authorization.authOps.push({ id: op[2], name: op[3], parentId: op[0] });
        });
        this.curResultGroup.authorizations.push(authorization);
      }

    });

    // DataStorage
    const entity = this.resultRepository.getEntityById(this.curResultGroup.groupId);
    entity.authorizations = this.curResultGroup.authorizations;
    console.log('closeAuthopsHelp:');
    console.log(entity.authorizations);

  }

  //  
  public get checkableSettings(): CheckableSettings {
    return {
      checkChildren: true,
      checkParents: true,
      enabled: true,
      mode: 'multiple'
    };
  }

  /**************************----------*********************************************/


  /**************************----------*********************************************/
  //  
  public get selectableSettings(): SelectableSettings {
    return {
      checkboxOnly: true,
      mode: 'multiple',
      enabled: true
    };
  }

  // 
  getCssData(): string {
    if (this.canEditData === true) {
      return 'btn btn-sm  mr-2  btn-primary';
    } else {
      return 'btn btn-sm  mr-2';
    }
  }


  ShowDatasHelp(groupId: string, canEdit: boolean): void {
    //  
    this.checkedDataKeys = [];
    this.isAllData = false;
    this.checkedDataKeys.forEach(data => console.log(data));
    this.canEditData = canEdit;
    this.curResultGroup = this.auresults.find(x => x.groupId === groupId);
    const findAll = this.curResultGroup.datas.find(x => x.data === '*');
    if (isNullOrUndefined(findAll)) {
      this.curResultGroup.datas.forEach((data) => {
        this.checkedDataKeys.push(data.data);
      });
    } else {
      this.isAllData = true;
      // TODO   
      this.canAssignDatas.forEach((data) => {
        this.checkedDataKeys.push(data.id);
      });
    }
    this.dataTreeNavDialog = true;
  }

  BuildDisplayData(data: any): string {
    if (isNullOrUndefined(data.find(x => x.data === '*'))) {
      return '';
    }
    return '';
  }

  closeDataHelp() {
    this.dataTreeNavDialog = false;
    this.curResultGroup.datas = [];
    if (this.isAllData) {
      const bizdataDto = new BizDataDTO();
      bizdataDto.data = '*';
      this.curResultGroup.datas.push(bizdataDto);
    } else {
      // TODO  assid1
      this.checkedDataKeys.forEach((dataitem) => {
        const bizdataDto = new BizDataDTO();
        bizdataDto.data = dataitem;
        this.curResultGroup.datas.push(bizdataDto);
      });
    }
    // DataStorage
    const entity = this.resultRepository.getEntityById(this.curResultGroup.groupId);
    entity.datas = this.curResultGroup.datas;
  }

  checkClick() {
    this.isAllData = !this.isAllData;
    if (this.isAllData) {
      // TODO   
      this.canAssignDatas.forEach((data) => {
        this.checkedDataKeys.push(data.id);
      });
    } else {
      this.checkedDataKeys = [];
    }
  }
  /**************************----------*********************************************/
}





