import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { CommandHandler, NgCommandHandler, TaskExecutionContext, EntityFactory } from '@farris/devkit';
import { AuresultStatemachine } from '../../app/auresult-statemachine';
import { AuresultBindingdata } from '../../app/auresult-bindingdata';
import { AuresultUistate } from '../../app/auresult-uistate';
import { AuresultService, AuresultRepository, AuresultEntity } from '../../domain/index';
import { isNullOrUndefined } from 'util';

import { AuthassignSession } from '../../../shared/session';

@Injectable()
@NgCommandHandler({
  commandName: 'add'
})
class AuresultAddHandler extends CommandHandler {

  constructor(
    private router: Router,
    private dataService: AuresultService,
    private uiState: AuresultUistate,
    private stateMachine: AuresultStatemachine,
    private repository: AuresultRepository,
    private bindingData: AuresultBindingdata,
    private session: AuthassignSession
  ) {
    super();
  }

  schedule() {

    // 
    this.addTask('add', (lastResult: any, context: TaskExecutionContext) => {
      // 
      const command = context.command;
      const packageid = this.session.packageId; // command.params.pakageId;
      const authfield = this.session.authField; // command.params.authfield;

      const groupIdNew = this.BuildNewGroup();
      this.uiState.action = 'add';

      const data = {
        packageId: packageid, // TODO Web
        groupId: groupIdNew, // TODO 
        authField: authfield, // TODO Web
        datas: [],
        authorizations: []
      };
      const newEntity = EntityFactory<AuresultEntity>(AuresultEntity, data);
      this.repository.addEntity(newEntity);
      console.log('' + groupIdNew);
      this.dataService.addGroups(groupIdNew);
    });

    this.addTask('transitState', () => {
      this.stateMachine['add']();
    });
  }

  BuildNewGroup(): string {
    const entities = this.repository.getAllEntities();
    if (isNullOrUndefined(entities) || entities.length === 0) {
      return '1';
    }

    let keys = [];

    entities.forEach((item) => {
      if (!isNaN(Number(item.groupId))) {
        keys.push(Number(item.groupId));
      }
    });
    if (isNullOrUndefined(keys) || keys.length === 0) {
      return '1';
    }
    const max = Math.max.apply(null, keys) + 1;
    return max;
  }
}

export { AuresultAddHandler };



