import { Injectable, Injector } from '@angular/core';
import { FormControl } from '@angular/forms';
import { NgForm, Form, NgFormControl, NgChildForm, NgChildFormArray } from '@farris/devkit';
import { AuthfieldTreeBindingdata} from './authfield-tree-bindingdata';

@Injectable()
@NgForm({
  bindingData: AuthfieldTreeBindingdata
})
class AuthfieldTreeForm extends Form {

  constructor(injector: Injector) {
    super(injector);
  }

  /**
   * id
   */
  @NgFormControl({
    name: 'Id',
    binding: 'FID'
  })
  Id: FormControl;

  /**
   * code
   */
  @NgFormControl({
    name: 'code',
    binding: 'FCode'
  })
  Code: FormControl;

  /**
   * 
   */
  @NgFormControl({
    name: 'name',
    binding: 'FName'
  })
  Name: FormControl;

  /**
   * 
   */
  @NgFormControl({
    name: 'type',
    binding: 'FType'
  })
  Type: FormControl;
}


export {AuthfieldTreeForm };



