import { Injectable, Injector } from '@angular/core';
import { NgBindingData, BindingData } from '@farris/devkit';
import { AuthfieldTreeRepository, AuthfieldEntity } from '../domain/index';

@Injectable()
@NgBindingData({
  entity: AuthfieldEntity,
  repository: AuthfieldTreeRepository
})
class AuthfieldTreeBindingdata extends BindingData {
  constructor(injector: Injector) {
    super(injector);
  }
}

export { AuthfieldTreeBindingdata
};



