import { Injectable, Injector } from '@angular/core';
import { NgRepository, Repository } from '@farris/devkit';
import { AuthFieldTreeDatastorage } from './authfield-tree-datastorage';
import { AuthfieldEntity } from './entities/authfield.entity';

@Injectable()
@NgRepository({
  from : AuthFieldTreeDatastorage,
  entityType : AuthfieldEntity
})
class AuthfieldTreeRepository extends Repository<AuthfieldEntity> {
  constructor(injector: Injector) {
    super(injector);
  }
}

export { AuthfieldTreeRepository };



