
import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler, TaskExecutionContext, ChangeType } from '@farris/devkit';
import { isNullOrUndefined } from 'util';
import { AuresultStatemachine } from '../auresult-statemachine';
import { AuresultBindingdata } from '../../app/auresult-bindingdata';
import { AuresultUistate } from '../../app/auresult-uistate';
import { AuresultService, AuresultRepository, AuresultEntity } from '../../domain/index';
import { AuresultChangesEntity, AuresultSave } from '../../domain/entities/auresut-changes.entity';

import { AuthassignSession } from '../../../shared/session';
import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es/utils';


@Injectable()
@NgCommandHandler({
  commandName: 'save'
})
class AuresultSaveHandler extends CommandHandler {

  constructor(
    private dataService: AuresultService,
    private uiState: AuresultUistate,
    private stateMachine: AuresultStatemachine,
    private repository: AuresultRepository,
    private bindingData: AuresultBindingdata,
    private session: AuthassignSession
  ) {
    super();

  }

  schedule() {

    // 
    this.addTask('save', () => {
      this.uiState.action = 'save';
      const auresultChanges = this.buildNewAuresultChangesEntity();
      this.repository.getAllEntities().forEach(item => {
        const index = this.session.addGroupIds.findIndex(x => x.toString() === item.groupId.toString());
        if (index > -1) {
          const result = this.buildAuresultSave(item);
          auresultChanges.addResult.push(result);
        } else if (isNullOrUndefined(item.changes) === false && item.changes.length !== 0) {
          const result = this.buildAuresultSave(item);
          auresultChanges.editResult.push(result);
        }
      });
      console.log('');
      console.log(auresultChanges);
      this.dataService.saveResultData(auresultChanges).subscribe();
      alert('');
      // 
      this.session.addGroupIds.length = 0;

    });
  }

  /**
  * 
  */
  buildNewAuresultChangesEntity(): AuresultChangesEntity {
    const auresultChanges: AuresultChangesEntity = new AuresultChangesEntity();
    auresultChanges.ownerId = this.session.ownerId; // TODO
    auresultChanges.authField = this.session.authField; // TODO
    auresultChanges.year = this.session.year; // TODO
    auresultChanges.addResult = new Array<any>();
    auresultChanges.removeResult = new Array<any>();
    auresultChanges.editResult = new Array<any>();
    auresultChanges.ownerType = this.session.ownerType; // TODO
    return auresultChanges;
  }

  // Entity  
  buildAuresultSave(item: AuresultEntity): AuresultSave {
    const result = new AuresultSave();
    result.packageId = item.packageId;
    result.authField = item.authField;
    result.authorizations = item.authorizations;
    result.datas = item.datas;
    result.groupId = item.groupId;
    result.ruleEntity = item.ruleEntity;
    return result;
  }
}

export { AuresultSaveHandler };



