import { Injectable, Injector } from '@angular/core';
import { FormControl } from '@angular/forms';
import { NgForm, Form, NgFormControl, NgChildForm, NgChildFormArray } from '@farris/devkit';
import { AuresultBindingdata} from './auresult-bindingdata';

@Injectable()
@NgForm({
  bindingData: AuresultBindingdata
})
class AuresultForm extends Form {

  constructor(injector: Injector) {
    super(injector);
  }

  /**
   * packageid
   */
  @NgFormControl({
    name: 'packageid',
    binding: 'packageid'
  })
  PackageID: FormControl;

  /**
   * authfield
   */
  @NgFormControl({
    name: 'authfield',
    binding: 'authfield'
  })
  Authfield: FormControl;

  /**
   * 
   */
  @NgFormControl({
    name: 'groupid',
    binding: 'groupid'
  })
  GroupID: FormControl;

  /**
   * 
   */
  @NgFormControl({
    name: 'datas',
    binding: 'datas'
  })
  DataCollection: FormControl;

  /**
   * Id
   */
  @NgFormControl({
    name: 'authorizations',
    binding: 'authorizations'
  })
  AuthopCollection: FormControl;
}


export {AuresultForm };



